import { Box, Div, Left, Li, Pr, Right, Sb, Title } from "./styled";

const history = [
  " : LED ",
  "KT CS  ",
  "()olulo KICKGOING",
  "() elecle",
  "",
  "",
  "  ",
];

const ocupy = [
  `  
     `,
  `  CS 
  `,
  ` 
      
    `,
  " ",
  "",
  "",
];

const list = history.map((a, i) => {
  return { company: a, ocupy: ocupy[i] };
});

const parent = {
  hidden: { opacity: 0 },
  visible: {
    opacity: 1,
    transition: {
      delay: 0.2,
      delayChildren: 0.3,
      staggerChildren: 0.5,
    },
  },
};

const child = {
  hidden: { y: 20, opacity: 0 },
  visible: {
    y: 0,
    opacity: 1,
  },
};

function History() {
  return (
    <Box id="Career">
        <>
          <Title variants={parent} initial={{opacity: 0}} animate="visible">
              <br />
            CAREER
          </Title>
          <Div variants={parent} initial="hidden" animate="visible">
            <Left>
              {list
                .filter((a, i) => i % 2 === 0)
                .map((a, i) => (
                  <Li key={i} variants={child}>
                    <Pr>{a.company}</Pr>
                    <Sb>{a.ocupy}</Sb>
                    <Pr />
                  </Li>
                ))}
            </Left>
            <Right>
              {list
                .filter((a, i) => i % 2 === 1)
                .map((a, i) => (
                  <Li key={i} variants={child}>
                    <Pr>{a.company}</Pr>
                    <Sb>{a.ocupy}</Sb>
                    <Pr />
                  </Li>
                ))}
            </Right>
          </Div>
        </>
    </Box>
  );
}

export default History;
